import * as React from "react";
import style from "./Modal.module.scss"
import {svgIcons} from "../../assets/svg/svgIcons";
import {ButtonIcon} from "../common/ButtonIcon/BtnIcon";
import {useRef} from "react";
import {useOutsideClick} from "../../hooks/useOutsideClick";
import {appAC} from "../../store/reducers/app.reducer";
import {useDispatch} from "react-redux";

const buttons = [
    {
        icon: svgIcons.metamask,
        label: "Metamask"
    },
    {
        icon: svgIcons.walletconnect,
        label: "Walletconnect"
    },
];

export const Modal = () => {
    const ref = useRef<HTMLDivElement>(null);
    const dispatch = useDispatch();
    useOutsideClick(ref, () => dispatch(appAC.setShowModal(false)));

    return (
        <div className={style.modal}>
            <div className={style.content} ref={ref}>
                <ButtonIcon className={style.btnClose}
                            icon={svgIcons.close}
                            onClick={() => dispatch(appAC.setShowModal(false))}
                />
                <h2 className={style.title}>Connect wallet</h2>
                <p className={style.subtitle}>Please select your provider</p>
                <div className={style.btns}>
                    {
                        buttons.map(({icon, label}, index) => (
                            <button key={index}
                                    onClick={() => {
                                        dispatch(appAC.setConnect(true));
                                        dispatch(appAC.setConnectInfo({
                                            link: "0x7c8d1c7c8d1c7c80x7c8d1c7c8d1c7c8",
                                            provider: label,
                                            icon
                                        }));
                                        dispatch(appAC.setShowModal(false));
                                    }}
                            >
                                {icon}
                                <p className={style.label}>{label}</p>
                            </button>
                        ))
                    }
                </div>
            </div>
        </div>
    )
}