import * as React from "react";
import style from "./Footer.module.scss";
import logo from "../../assets/png/footer/footer-logo.png";
import {links} from "../A1_Header/Header";
import {socialIcons} from "../A3_BurgerMenu/socialIcons";
import {HashLink} from "react-router-hash-link";

export const Footer = () => {
    return (
        <footer className={style.footer}>
            <img src={logo} alt="" className={style.logo}/>

            <div className={style.backMobile}/>
            <div className={style.backDesktop}/>

            <div className={style.content}>

                <nav className={style.links}>
                    {
                        links.map(({label, to}, index) => {
                                if (label === "Whitelisting") {
                                    return (
                                        <a key={index}
                                           href={to}
                                           className={style.link}
                                           target="_blank"
                                        >
                                            {label}
                                        </a>
                                    )
                                } else {
                                    return <HashLink key={index}
                                                     to={to}
                                                     className={style.link}
                                                     smooth={true}
                                    >
                                        {label}
                                    </HashLink>
                                }
                            }
                        )
                    }
                </nav>


                <div className={style.socialIcons}>
                    {
                        socialIcons.map(({icon, href}, index) => (
                                <a key={index} href={href} className={style.socialLink}>
                                    {icon}
                                </a>
                            )
                        )
                    }
                </div>

                <p className={style.copy}>@2022 CroCactus version 1.0.0-81f12ac6</p>

            </div>

        </footer>
    )
}