interface ISlide {
    title: string
    text: string
    step: string
    background: string
}


export const slides = [] as ISlide[];

cards.forEach(cardsBlock => {
    cardsBlock.cards.forEach(card => {
        slides.push({
            title: card.title,
            text: card.text,
            step: cardsBlock.step,
            background: cardsBlock.background
        })
    })
})