import * as React from "react";
import style from "./MintPage.module.scss";
import {H1} from "../common/H1/H1";
import {useState} from "react";
import {ButtonPlusMinus} from "../common/ButtonPlusMinus/ButtonPlusMinus";
import {ButtonMint} from "../common/ButtonMint/ButtonMint";

export const MintPage = () => {
    const [count, setCount] = useState(0);

    return (
        <div className={style.mintPage} id="top">
            <H1 title="Mint"/>

            <div className={style.status}>
                <div className={style.inner}>
                    <div className={style.box}/>
                    <p className={style.text}>0/3,232</p>
                </div>
            </div>

            <p className={style.price}>price = 555 CRO</p>


            <div className={style.countWrapper}>

                <div className={style.countBlock}>
                    <ButtonPlusMinus text="-"
                                     disabled={count === 0}
                                     onClick={() => setCount(count - 1)}
                    />
                    <p className={style.count}>{count}</p>
                    <ButtonPlusMinus text="+"
                                     disabled={count === 5}
                                     onClick={() => setCount(count + 1)}
                    />
                </div>

                <ButtonMint className={style.btnMint}/>

            </div>



        </div>
    )
}
