import * as React from "react";
import style from "./ButtonConnect.module.scss"
import clsx from "clsx";
import {FC, useState} from "react";

interface IButton extends React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> {
    className?: string
}

export const ButtonConnect: FC<IButton> = ({
                                            className,
                                            ...props
                                        }) => {
    const [mouseDown, setMouseDown] = useState(false);

    return (
        <button className={clsx({
            [style.buttonConnect]: true,
            [style.buttonConnect_mouseDown]: mouseDown,
        }, className && className)}
                onMouseDown={() => setMouseDown(true)}
                onMouseUp={() => setMouseDown(false)}
                {...props}
        >
            <p>connect</p>
        </button>
    )
}