import {FC} from "react";
import * as React from "react";
import style from "./BurgerMenu.module.scss"
import {links} from "../A1_Header/Header";
import {ButtonConnect} from "../common/ButtonConnect/ButtonConnect";
import {socialIcons} from "./socialIcons";
import clsx from "clsx";
import {HashLink} from "react-router-hash-link";
import {appAC, selectConnect, selectConnectInfo} from "../../store/reducers/app.reducer";
import {useDispatch, useSelector} from "react-redux";
import {Account} from "../A5_Account/Account";

interface IBurgerMenu {
    menuOpen: boolean
    onClose: () => void
}

export const BurgerMenu: FC<IBurgerMenu> = ({menuOpen, onClose}) => {
    const dispatch = useDispatch();
    const connect = useSelector(selectConnect);
    const connectInfo = useSelector(selectConnectInfo);

    return (
        <div className={clsx({
            [style.burgerMenu]: true,
            [style.burgerMenu_show]: menuOpen,
        })}>
            <div className={style.inner}>

                <div className={style.links}>
                    {
                        links.map(({label, to}, index) => {
                                if (label === "Whitelisting") {
                                    return (
                                        <a key={index}
                                           href={to}
                                           className={style.link}
                                           target="_blank"
                                           onClick={onClose}
                                        >
                                            <span>{label}</span>
                                        </a>
                                    )
                                } else {
                                    return <HashLink key={index}
                                                     to={to}
                                                     className={style.link}
                                                     smooth={true}
                                                     onClick={onClose}
                                    >
                                        <span>{label}</span>
                                    </HashLink>
                                }
                            }
                        )
                    }
                </div>

                {
                    !connect && <ButtonConnect className={style.btnConnect}
                                               onClick={() => dispatch(appAC.setShowModal(true))}
                    />
                }

                {
                    connect && connectInfo && <Account className={style.account}/>
                }


                <div className={style.socialIcons}>
                    {
                        socialIcons.map(({icon, href}, index) => (
                                <a key={index} href={href} className={style.link}>
                                    {icon}
                                </a>
                            )
                        )
                    }
                </div>


            </div>
        </div>
    )
}