import * as React from "react";
import style from "./Header.module.scss"
import logo from "../../assets/png/header-logo.png";
import {ButtonConnect} from "../common/ButtonConnect/ButtonConnect";
import {useEffect, useState} from "react";
import {throttle} from "../../helpers/throttle";
import clsx from "clsx";
import {HashLink} from "react-router-hash-link";
import {ButtonIcon} from "../common/ButtonIcon/BtnIcon";
import {svgIcons} from "../../assets/svg/svgIcons";
import {Account} from "../A5_Account/Account";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectConnect, selectConnectInfo, selectMenuOpen} from "../../store/reducers/app.reducer";

export const links = [
    {label: "Rarity", to: "/#Rarity"},
    {label: "Roadmap", to: "/#Roadmap"},
    {label: "Team", to: "/#Team"},
    {label: "Mint", to: "/Mint#top"},
    {label: "Whitelisting", to: "https://forms.gle/QW6pZWkKfXvX1jVJ6"},
];

export const Header = () => {
    const [pageYOffset, setPageYOffset] = useState(0);
    const dispatch = useDispatch();
    const connect = useSelector(selectConnect);
    const connectInfo = useSelector(selectConnectInfo);
    const menuOpen = useSelector(selectMenuOpen);

    useEffect(() => {
        document.addEventListener(
            'scroll',
            throttle(() => {
                setPageYOffset(window.pageYOffset);
            }, 500)
        );
        return document.removeEventListener(
            'scroll',
            throttle(() => {
                setPageYOffset(window.pageYOffset);
            }, 500)
        );

    });

    return (
        <header className={clsx({
            [style.header]: true,
            [style.header_isSliding]: pageYOffset > 0
        })}>
            <HashLink to="/#home"
                      className={style.logo}
                      onClick={() => dispatch(appAC.setMenuOpen(false))}
                      smooth={true}>
                <img src={logo} alt=""/>
            </HashLink>

            <div className={style.links}>
                {
                    links.map(({label, to}, index) => {
                            if (label === "Whitelisting") {
                                return (
                                    <a key={index}
                                       href={to}
                                       className={style.link}
                                       target="_blank"
                                    >
                                    <span>{label}</span>
                                    </a>
                                )
                            } else {
                                return <HashLink key={index}
                                                 to={to}
                                                 className={style.link}
                                                 smooth={true}
                                >
                                    <span>{label}</span>
                                </HashLink>
                            }
                        }
                    )
                }
            </div>

            {
                connect && connectInfo && <Account className={style.account}/>
            }

            {
                !connect && <ButtonConnect className={style.btnConnect}
                                           onClick={() => dispatch(appAC.setShowModal(true))}
                />
            }

            <ButtonIcon className={style.btnBurger}
                        icon={menuOpen ? svgIcons.close : svgIcons.burger}
                        onClick={() => dispatch(appAC.setMenuOpen(!menuOpen))}
            />

        </header>
    )
}