import * as React from "react";
import {FC, useRef} from "react";
import style from "./Account.module.scss"
import clsx from "clsx";
import {svgIcons} from "../../assets/svg/svgIcons";
import {useOutsideClick} from "../../hooks/useOutsideClick";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectConnectInfo, selectShowAccount} from "../../store/reducers/app.reducer";

interface IAccount {
    className?: string
}

export const Account: FC<IAccount> = ({
                                          className,
                                      }) => {
    const showAccount = useSelector(selectShowAccount);
    const connectInfo = useSelector(selectConnectInfo);
    const dispatch = useDispatch();
    const ref = useRef<HTMLDivElement>(null);

    useOutsideClick(ref, () => {
        //console.log("useOutsideClick");
        //dispatch(appAC.setShowAccount(false));
    });

    return (
        <div className={clsx(style.btnAccountWrapper, className && className)}>

            {
                connectInfo &&
                <button className={style.btnAccount}
                        onClick={() => dispatch(appAC.setShowAccount(!showAccount))}
                >
                    <div className={style.iconProvider}>
                        {connectInfo.icon}
                    </div>
                    <p>{connectInfo.link}</p>
                    <div className={clsx({
                        [style.iconArrow]: true,
                        [style.iconArrow_showAccount]: showAccount,
                    })}>
                        {svgIcons.arrow_down}
                    </div>
                </button>
            }

            {
                showAccount && connectInfo && (
                    <div className={style.account}
                         //ref={ref}
                         //onClick={() => console.log("on account click")}
                    >
                        <div className={style.top} ref={ref}>
                            <p>Account</p>
                            <button onClick={() => {
                                //console.log("disconnect")
                                dispatch(appAC.setConnect(false));
                                dispatch(appAC.setConnectInfo(null));
                                dispatch(appAC.setShowAccount(false));
                            }}>
                                Disconnect
                            </button>
                        </div>

                        <div className={style.infoWrapper}>
                            <div className={style.iconWrapper}>
                                {connectInfo.icon}
                            </div>
                            <div className={style.info}>
                                <p>{connectInfo.link}</p>
                                <p>{connectInfo.provider}</p>
                            </div>
                        </div>
                    </div>
                )
            }
        </div>
    )
}