import * as React from "react";
import style from "./ButtonJoin.module.scss"
import clsx from "clsx";
import {svgIcons} from "../../../assets/svg/svgIcons";
import {FC, useState} from "react";

interface IButtonJoin extends React.DetailedHTMLProps<React.AnchorHTMLAttributes<HTMLAnchorElement>, HTMLAnchorElement> {
    className?: string
}

export const ButtonJoin: FC<IButtonJoin> = ({
                                                className,
                                                ...props
                                            }) => {
    const [mouseDown, setMouseDown] = useState(false);

    return (
        <a className={clsx({
            [style.buttonJoin]: true,
            [style.buttonJoin_mouseDown]: mouseDown,
        }, className && className)}
           onMouseDown={() => setMouseDown(true)}
           onMouseUp={() => setMouseDown(false)}
           href="https://discord.gg/X4696pbY"
           target="_blank"
           {...props}
        >
            {svgIcons.discordBtn}
            <p>Join discord</p>
        </a>
    )
}