import React from 'react';
import style from "./App.module.scss"
import {Header} from "../A1_Header/Header";
import {Route, Routes} from "react-router-dom";
import {Footer} from "../A2_Footer/Footer";
import {HomePage} from "../B0_HomePage/HomePage";
import {MintPage} from "../B1_MintPage/MintPage";
import {BurgerMenu} from "../A3_BurgerMenu/BurgerMenu";
import clsx from "clsx";
import {Modal} from "../A4_Modal/Modal";
import {useDispatch, useSelector} from "react-redux";
import {appAC, selectMenuOpen, selectShowModal} from "../../store/reducers/app.reducer";

export const App = () => {
    const dispatch = useDispatch();
    const menuOpen = useSelector(selectMenuOpen);
    const showModal = useSelector(selectShowModal);

    return (
        <div className={clsx({
            [style.app]: true,
            [style.app_fixed]: menuOpen || showModal,
        })}>
            <BurgerMenu menuOpen={menuOpen}
                        onClose={() => dispatch(appAC.setMenuOpen(false))}
            />

            {
                showModal && <Modal />
            }

            <div className={clsx({
                [style.content]: true,
                [style.content_showModal]: showModal,
            })}>
                <Header />

                <Routes>
                    <Route path="/" element={<HomePage/>}/>
                    <Route path="/mint" element={<MintPage/>}/>
                </Routes>

                <Footer/>
            </div>



        </div>
    );
}

