import {GetActionsType, StateType} from "../store";

//================ TYPE ==================
export type InitialStateType = typeof initialState;
export type AppActionsType = GetActionsType<typeof appAC>

export interface IConnectInfo {
    link: string
    provider: string
    icon: JSX.Element
}


//================ INITIAL ==================
const initialState = {
    menuOpen: false,
    showModal: false,
    connect: false,
    connectInfo: null as null | IConnectInfo,
    showAccount: false,
};

//================ REDUCER ===================
export const appReducer = (state = initialState, action: AppActionsType): InitialStateType => {
    switch (action.type) {
        case 'CACTUS/APP/SET_MENU_OPEN': {
            return {...state, menuOpen: action.menuOpen}
        }
        case 'CACTUS/APP/SET_SHOW_MODAL': {
            return {...state, showModal: action.showModal}
        }
        case 'CACTUS/APP/SET_CONNECT': {
            return {...state, connect: action.connect}
        }
        case 'CACTUS/APP/SET_CONNECT_INFO': {
            return {...state, connectInfo: action.connectInfo}
        }
        case 'CACTUS/APP/SET_SHOW_ACCOUNT': {
            return {...state, showAccount: action.showAccount}
        }
        default:
            return state;
    }
};

// ================ ACTION CREATORS =================
export const appAC = {
    setMenuOpen: (menuOpen: boolean) => ({type: 'CACTUS/APP/SET_MENU_OPEN', menuOpen} as const),
    setShowModal: (showModal: boolean) => ({type: 'CACTUS/APP/SET_SHOW_MODAL', showModal} as const),
    setConnect: (connect: boolean) => ({type: 'CACTUS/APP/SET_CONNECT', connect} as const),
    setConnectInfo: (connectInfo: IConnectInfo | null) => ({type: 'CACTUS/APP/SET_CONNECT_INFO', connectInfo} as const),
    setShowAccount: (showAccount: boolean) => ({type: 'CACTUS/APP/SET_SHOW_ACCOUNT', showAccount} as const),
}
//================ THUNK CREATORS ==================

//============== SELECTORS =================
export const selectMenuOpen = (state: StateType) => state.app.menuOpen;
export const selectShowModal = (state: StateType) => state.app.showModal;
export const selectConnect = (state: StateType) => state.app.connect;
export const selectConnectInfo = (state: StateType) => state.app.connectInfo;
export const selectShowAccount = (state: StateType) => state.app.showAccount;