import * as React from "react";
import style from "./Team.module.scss"
import {H2} from "../common/H2/H2";
import {cards} from "./cards";
import lizard from "../../assets/png/team/lizard.png";
import ReactTooltip from "react-tooltip";

export const Team = () => {
    return (
        <section className={style.team} id="Team">
            <H2 title="team"/>
            <div className={style.content}>
                {
                    cards.map(({src, name, username, job, background, toolTip}, index) => (
                        <div className={style.card} key={index}>

                            <img src={src} alt="" className={style.person}/>
                            {index === 0 && <img src={lizard} alt="" className={style.lizardMobile}/>}
                            {index === 1 && <img src={lizard} alt="" className={style.lizardDesktop}/>}

                            <div className={style.inner}>
                                <p className={style.name}>{name}</p>
                                <div className={style.bottom}>
                                    <p className={style.job}
                                       style={{
                                           background
                                       }}
                                    >
                                        {job}
                                    </p>

                                    <p className={style.readMoreMobile}
                                       data-tip data-for={`tooltipMobile${index}`}
                                       data-event='click focus'
                                    >
                                        {username}
                                    </p>

                                    <p className={style.readMoreDesktop}
                                       data-tip data-for={`tooltipDesktop${index}`}
                                    >
                                        {username}
                                    </p>

                                    <ReactTooltip id={`tooltipMobile${index}`}
                                                  type='light'
                                                  place="top"
                                                  effect="solid"
                                                  globalEventOff='click'
                                                  offset={{top: 0, left: 100}}
                                                  arrowColor="transparent"
                                                  className={style.toolTipWrapper}
                                    >
                                        <p className={style.toolTip}>
                                            {toolTip}
                                        </p>
                                    </ReactTooltip>

                                    <ReactTooltip id={`tooltipDesktop${index}`}
                                                  type='light'
                                                  place="top"
                                                  effect="solid"
                                                  globalEventOff='click'
                                                  //offset={{top: 0, left: 55}}
                                                  //arrowColor="transparent"
                                                  className={style.toolTipWrapper}
                                    >
                                        <p className={style.toolTip}>
                                            {toolTip}
                                        </p>
                                    </ReactTooltip>

                                </div>

                            </div>

                        </div>
                    ))
                }
            </div>
        </section>
    )
}
