import * as React from "react";
import style from "./ButtonMint.module.scss"
import clsx from "clsx";
import {FC, useState} from "react";

interface IButton extends React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> {
    className?: string
}

export const ButtonMint: FC<IButton> = ({
                                            className,
                                            ...props
                                        }) => {
    const [mouseDown, setMouseDown] = useState(false);

    return (
        <button className={clsx({
            [style.buttonMint]: true,
            [style.buttonMint_mouseDown]: mouseDown,
        }, className && className)}
                onMouseDown={() => setMouseDown(true)}
                onMouseUp={() => setMouseDown(false)}
                {...props}
        >
            <p>mint nft</p>
        </button>
    )
}