import * as React from "react";
import style from "./Roadmap.module.scss";
import {H2} from "../common/H2/H2";
import {cards, slides} from "./cards";
import imgLeft from "../../assets/png/roadmap/back-img-left.png";
import imgRight from "../../assets/png/roadmap/back-img-right.png";

import "swiper/css";
import SwiperClass from 'swiper/types/swiper-class';
import {Swiper, SwiperSlide} from "swiper/react";
import {useState} from "react";
import clsx from "clsx";

export const Roadmap = () => {
    const [swiper, setSwiper] = useState<SwiperClass | null>(null);
    const [currentIndex, setCurrentIndex] = useState(0);

    return (
        <section className={style.roadmap} id="Roadmap">
            <H2 title="Roadmap"/>

            <img src={imgLeft} alt="" className={style.imgLeft}/>
            <img src={imgRight} alt="" className={style.imgRight}/>

            <div className={style.cardsMobile}>

                <div className={style.step}
                     style={{background: slides[currentIndex].background}}
                >
                    {slides[currentIndex].step}
                </div>

                <Swiper className={style.swiper}
                        slidesPerView="auto"
                        onSwiper={(swiper) => {
                            setSwiper(swiper);
                        }}
                        onSlideChange={swiper => setCurrentIndex(swiper.realIndex)}
                        spaceBetween={10}
                >
                    {
                        slides.map(({title, text}, index) => (
                            <SwiperSlide className={style.slide} key={index}>
                                <div className={style.innerSlide}>
                                    <p className={style.title}>{title}</p>
                                    <p className={style.text}>{text}</p>
                                </div>
                                <div className={style.shadow}/>
                            </SwiperSlide>
                        ))
                    }
                </Swiper>

                <div className={style.dots}>
                    {
                        slides.map((_, index) => (
                            <div className={clsx({
                                [style.dot]: true,
                                [style.dot_selected]: currentIndex === index,
                            })}
                                 onClick={() => swiper?.slideTo(index)}
                                 key={index}
                            />
                        ))
                    }
                </div>

            </div>

            <div className={style.cardsDesktop}>
                {
                    cards.map(({step, background, cards}, index) => (
                        <div className={style.cardsBlock} key={index}>

                            <div className={style.cards}>

                                <div className={style.step}
                                     style={{background}}
                                >
                                    {step}
                                </div>


                                <div className={style.inner}>
                                    {
                                        cards.map(({title, text}, index) => (
                                            <div className={style.card} key={index}>
                                                <p className={style.title}>{title}</p>
                                                <p className={style.text}>{text}</p>
                                            </div>
                                        ))
                                    }
                                </div>

                            </div>
                        </div>
                    ))
                }
            </div>

        </section>
    )
}