import * as React from "react";
import style from "./ButtonPlusMinus.module.scss"
import clsx from "clsx";
import {FC, useState} from "react";

interface IButton extends React.DetailedHTMLProps<React.ButtonHTMLAttributes<HTMLButtonElement>, HTMLButtonElement> {
    className?: string
    text: string
}

export const ButtonPlusMinus: FC<IButton> = ({
                                                 className,
                                                 text,
                                                 ...props
                                             }) => {
    const [mouseDown, setMouseDown] = useState(false);

    return (
        <button className={clsx({
            [style.buttonPlusMinus]: true,
            [style.buttonPlusMinus_mouseDown]: mouseDown,
        }, className && className)}
                onMouseDown={() => setMouseDown(true)}
                onMouseUp={() => setMouseDown(false)}
                {...props}
        >
            <p>{text}</p>
        </button>
    )
}